.about-me-section{
    padding-bottom: 2.3rem;
}

.about-me-col h3{
    font-size: 1.8rem;
    display: inline-block;
}

.img-col{
    align-self: center;
}

.no-section{
    color: #d61313;
}

.no-section, .separator-section{
    font-size: 1.8rem;
}

.img-perfil{
    /* height: 31%; */
    max-width: 100%;
    object-fit: cover;
    width: 100%;
    object-position: 50% 10%;
}

.skills{
    width: 100%;
}

.sub-skills{
    margin-left: 3rem;
}

.skills>.skill-item{
    display: inline-block;
    width: 45%;
    margin-right: 1rem;
}