.btn-open-drawer{
    position: fixed;
    top: 5%;
    left: 0px;
    z-index: 5;
    background-color: #fff;
    border: none;
    color: #000;
    border-radius: 0px 4px 4px 0px;
    box-shadow: 1px 1px 5px #313131;
}

.btn-close-drawer{
    position: absolute;
    top: 5%;
    right: 0px;
    z-index: 5;
    background-color: #0c0d0f;
    border: none;
    color: #FFF;
    border-radius: 4px 0px 0px 4px;
}