.experience-section{
    padding-top: 1.4rem;
    padding-bottom: 2rem;
    background-color: #F7F7F5;
    color: #000;
}

.experience-section .no-section{
    color: #E1BB88;
}

.experience-section h3{
    font-size: 1.8rem;
    display: inline-block;
}

.list {
    list-style: none;    
    max-width: 90%;
}

.item {
    display: block;
    clear: both;
    counter-increment: list;
    padding-bottom: 4rem;
    font-size: 1.1rem;
    line-height: 1.375;
    position: relative;
}

.item:before {
    font: bold 1.6rem Montserrat, Roboto, Helvetica, Arial, sans-serif;
    content: "";
    width: 5rem;
    height: 5rem;
    float: left;
    margin: 0 1.5rem 0.75rem 0;
    color: #fdfdfd;
    background: #fdfdfd linear-gradient(to bottom right, #E1BB88 25%, #dcc875);
    text-shadow: 0 0 2px #fdfdfd;
    border-radius: 50%;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    shape-outside: ellipse();
    z-index: 1;
    position: inherit;
}

.item:nth-child(1):before{
    content: "2015";
}

.item:nth-child(2):before{
    content: "2015";
}

.item:nth-child(3):before{
    content: "2016";
}

.item:nth-child(4):before{
    content: "2020";
}

.item:after {
    width: 2.5rem;
    height: 2.5rem;
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    background: #E1BB88;
    z-index: 0;
    border-top-left-radius: 3px;
}


.headline {
    padding: 0rem 0 0 0;
    margin: 0 0 1rem 0;
    font: bold 1.3rem Montserrat, Roboto, Helvetica, Arial, sans-serif
}

.img-clipart{
    max-width: 100%;
}