.wrap{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    height: 70vh;
    z-index: 2;
    position: relative;
}

.wrap:after{
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #00000057;
    z-index: -1;
}

.wrap h2{
    font-size: 3rem;
    color: #FFF;
}

.wrap .type-wrap{
    color: #FFF;
    font-size: 2rem;
}